import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { SharedModule } from '@shared/shared.module';

import { CompanyBankInfoComponent } from './company-bank-info.component';
import { PageLeaveGuard } from '@core/routing-guards';

const routes: Routes = [
  { path: '', pathMatch: 'full', component: CompanyBankInfoComponent, canDeactivate: [ PageLeaveGuard ] }
];

@NgModule({
  imports: [SharedModule, RouterModule.forChild(routes)],
  declarations: [CompanyBankInfoComponent ]
})
export class CompanyBankInfoModule {}
