import { Component, OnInit, ViewChild } from '@angular/core';
import { NgForm } from '@angular/forms';

import { combineLatest, Observable } from 'rxjs';
import { concatMap, filter, map, take, tap } from 'rxjs/operators';

import { areDifferent } from '@core';
import { BankRoutingNumberService, Saver, SaverFactoryService, WellKnownRoutes } from '@core';
import { Company } from '@model';
import { CompanyBankInfoVm } from './company-bank-info-vm';
import { CompanyService } from '@entity';
import { ModelComponent } from '@shared';
import { SessionService } from '@session';
import { ValidationService } from '@validation';

@Component({
  selector: 'sp-co-bank-info',
  templateUrl: './company-bank-info.component.html'
})
export class CompanyBankInfoComponent implements OnInit {

  @ViewChild('form') form: NgForm;
  @ViewChild(ModelComponent) modelComponent: ModelComponent;

  back: string = WellKnownRoutes.CompanyTaxInfo;
  next: string = WellKnownRoutes.Dashboard;

  private currentCompany$: Observable<Company>;

  vm$: Observable<CompanyBankInfoVm>;
  private vm: CompanyBankInfoVm;

  constructor(
    private companyService: CompanyService,
    private bnrService: BankRoutingNumberService,
    private saverFactory: SaverFactoryService,
    private sessionService: SessionService,
    private validationService: ValidationService
  ) {
    this.currentCompany$ = companyService.currentCompany$.pipe(filter(c => c != null));
  }

  ngOnInit(): void {
    window.scrollTo(0, 0);

    this.vm$ = combineLatest(
       this.currentCompany$,
       this.sessionService.isAdmin$
    ).pipe(
      map(([company, isAdmin]) => {
        return this.vm = this.makeVm(company, isAdmin);
      }),
      tap(_ => this.bankLookup()),
      take(1)
    );
  }

  bankLookup() {
    this.vm.bankName = this.vm.bankNameDisplay = ''; // clear existing bank name and its display
    this.bnrService.getBankName(this.vm.bankAccountRn).subscribe((bn: string) => {
      if (bn) {
        this.vm.bankName = this.vm.bankNameDisplay = bn;
      } else {
        this.vm.bankNameDisplay = `No bank for ${this.vm.bankAccountRn}`;
      }
      const component = this.modelComponent;
      if (component) {
        component.notifyOfModelChange(); // re-validates and updates the view
      }
    });
  }

  canLeaveAfterSave() {
    return this.vm ? this.save() : true;
  }

  get isInvalid() {
    return this.form.invalid || this.vm.bankAccountNumber !== this.vm.bankAccountNumberConfirm;
  }

  makeVm(company: Company, isAdmin: boolean): CompanyBankInfoVm {
    return {
      id: company.id,
      bankName: company.bankName,
      bankAccountRn: company.bankAccountRn,
      bankAccountNumber: company.bankAccountNumber,

      bankNameDisplay: company.bankName,
      bankAccountNumberConfirm: company.bankAccountNumber,
      isAdmin,
    };
  }

  private save() {
    const saver = this.saverFactory.create();
    saver.add(this.saveCompany());
    return saver.go();
  }

  private saveCompany() {
    return this.currentCompany$.pipe(
      take(1),
      concatMap(oldCompany => {
        // Save current cached company merged with cleaned-up properties that were presented.
        const vm = this.vm;
        const newCompany = {
          ...oldCompany,
          bankName: vm.bankName,
          bankAccountRn: vm.bankAccountRn,
          bankAccountNumber: vm.bankAccountNumber,
          // TODO: Remove from Company. Per Zorian, can only ever be checking account
          bankAccountType: 'checking' as 'checking'
        };
        const changed = areDifferent(newCompany, oldCompany, 'Company');
        return changed ? this.companyService.update(newCompany) : Saver.NO_SAVE;
      })
    );
  }
}
